const axios = require("axios");

// import JWT from "./jwt";

// eslint-disable-next-line
const PCors = "https://my.${region}.500apps.com/pcors";

const Region = window.region;

export default class Data {
  constructor(url, params) {
    // eslint-disable-next-line
    let region = Region;
    this.url = eval("`" + url + "`");

    if (params) this.params = params;

    // if (jwt) this.jwt = jwt;

    this.pcors = true;

    this.offset = 0;
    this.limit = 50;

    // Enable Offset
    this.enableOffset = false;
  }

  useOffset() {
    this.enableOffset = true;
    this.limit = 25;
  }

  disablePCors() {
    this.pcors = false;
  }

  // getJWT() {
  //   if (this.jwt) return this.jwt;
  //   return JWT.getJWT();
  // }

  getConfig() {
    return {
      headers: {
        // Authorization: `Bearer ${this.getJWT()}`,
        withCredentials: true,
      },
    };
  }

  getPCors(url) {
    if (!url) url = this.url;

    // Return URL directly if pcors is disabled
    if (!this.pcors) return url;

    // Use base url if not present - detail URL eg: /flow/{id} can overwrite
    let PCors_URL = `${PCors}?url=${encodeURIComponent(url)}`;
    PCors_URL = eval("`" + PCors_URL + "`");
    return PCors_URL;
  }

  getOffset(url) {
    if (!this.enableOffset) return url;
    url = this.append(url, `offset=${this.offset}`);
    return url;
  }

  setOffset(offset) {
    this.offset = offset;
  }

  incrementOffset() {
    this.offset += this.limit;
  }

  getLimit(url) {
    url = this.append(url, `limit=${this.limit}`);
    return url;
  }

  getParams(url) {
    if (!this.params) return url;
    return this.append(url, this.params);
  }

  setParams(params) {
    if (params) this.params = params;
  }
  append(url, params) {
    if (!url) url = "";
    let prepend = "?";
    if (url.indexOf("?") != -1) prepend = "&";

    url = `${url}${prepend}${params}`;
    return url;
  }

  async get() {
    console.log("Getting", this.url);

    let url = this.url;
    url = this.getLimit(url);
    url = this.getOffset(url);
    url = this.getParams(url);

    /*let url = null;
    if (this.params) url = `${this.url}?${this.params}`;*/

    let result = await axios.get(this.getPCors(url), this.getConfig());
    result = result.data;

    // Increment the offset
    this.incrementOffset();

    return result;
  }

  async get_details(id) {
    console.log("Getting", this.url, id);

    let url = `${this.url}/${id}`;

    let result = await axios.get(this.getPCors(url), this.getConfig());
    result = result.data;
    return result;
  }

  async add(form) {
    console.log("Adding new", this.url, form);
    let apps = await axios.post(this.getPCors(), { data: [form] }, this.getConfig());
    return apps.data;
  }

  async edit(id, formData) {
    console.log("Editing", this.url, id, formData);
    if (!formData.id) formData.id = id;
    let apps = await axios.put(this.getPCors(), formData, this.getConfig());
    return apps.data;
  }

  async delete(id) {
    console.log("Deleting", this.url, id);
    let url = `${this.url}?where=id=${id}`;
    let result = await axios.delete(url, this.getConfig());
    return result;
  }

  async count() {
    // After v2 add aggregate and / count at the end
    let url = `${this.url.replace("/v2", "/v2/aggregate")}/count`;
    let result = await axios.get(this.getPCors(url), this.getConfig());
    result = result.data;
    return result[0].count;
  }
  // Change the form to the form data
  async editForm(id, form) {
    let formData = new FormData();

    Object.keys(form).forEach((key) => {
      formData.append(key, form[key]);
    });

    // console.log(apps.data);
    let result = await this.edit(id, formData);
    return result;
  }
  // Method for multicalls
  async multi_call(form) {
    console.log("Adding new", this.url, form);
    this.url = this.getParams(this.url);
    let apps = await axios.post(this.getPCors(), form, this.getConfig());
    return apps.data;
  }

  // Page Cache Clear
  async pageCacheClear() {
    let config = {
      headers: {
        "secret-header": true,
      },
    };
    let result = await axios.head(this.getPCors(), config);
    return result;
  }

  getURL() {
    return this.url;
  }
  getRegion() {
    return Region;
  }
}
